import '../static/css/qrCode.css';
import meQrcode from "../components/common/meQrcode.vue";
export default {
	data() {
		return {
			ifShow: true,
			val: 'www.baidu.com', // 要生成的二维码值
			size: 200, // 二维码大小
			unit: 'upx', // 单位
			background: '#ffffff', // 背景色
			foreground: '#000000', // 前景色
			pdground: '#000000', // 角标色
			icon: '', // 二维码图标
			iconsize: 40, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: false, // val值变化时自动重新生成二维码
			loadMake: true, // 组件加载完成后自动生成二维码
			src: '', // 二维码生成后的图片地址或base64
			sliderMin: 50,
			sliderMax: 500,
			picBase: []
		}
	},
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#63b4fe'
		})
	},
	mounted(){
		uni.showToast({
			title: '修改自动触发生成二维码'
		})
	},
	watch:{
		val(newData,oldData){
			this.creatQrcode();
		},
		background(newData,oldData){
			const reg = "^#([0-9a-fA-F]{6}|[0-9a-fA-F]{3})$";
			if(newData.match(reg) === null){
				uni.showToast({
					title: '请正确输入十六进制颜色值'
				});
				return;
			}else{
				this.creatQrcode();
			}
		},
		foreground(newData,oldData){
			const reg = "^#([0-9a-fA-F]{6}|[0-9a-fA-F]{3})$";
			if(newData.match(reg) === null){
				uni.showToast({
					title: '请正确输入十六进制颜色值'
				});
				return;
			}else{
				this.creatQrcode();
			}
		},
		pdground(newData,oldData){
			const reg = "^#([0-9a-fA-F]{6}|[0-9a-fA-F]{3})$";
			if(newData.match(reg) === null){
				uni.showToast({
					title: '请正确输入十六进制颜色值'
				});
				return;
			}else{
				this.creatQrcode();
			}
		}
	},
	methods:{
		sliderchange(e) {
			this.size = e.detail.value;
		},
		creatQrcode() {
			this.$refs.qrcode._makeCode();
		},
		saveQrcode() {
			this.$refs.qrcode._saveCode();
		},
		qrR(res) {
			this.src = res
		},
		clearQrcode() {
			this.$refs.qrcode._clearCode()
			this.val = ''
		},
		ifQrcode() {
			this.ifShow = !this.ifShow
		},
		selectIcon() {
			let that = this
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function (res) {
					that.icon = res.tempFilePaths[0];
					that.picBase.push(that.icon);
					setTimeout(() => {
						that.creatQrcode()
					}, 100);
				}
			});
		},
		removeIcon(){
			this.picBase = [];
			this.icon = '';
			const self = this;
			setTimeout(() => {
				self.creatQrcode()
			}, 100);
		}
	},
	components:{
		meQrcode
	}
}
